module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-layout-basic',\n  template: `\n    <nz-layout>\n      <nz-header>Header</nz-header>\n      <nz-content>Content</nz-content>\n      <nz-footer>Footer</nz-footer>\n    </nz-layout>\n\n    <nz-layout>\n      <nz-header>Header</nz-header>\n      <nz-layout>\n        <nz-sider>Sider</nz-sider>\n        <nz-content>Content</nz-content>\n      </nz-layout>\n      <nz-footer>Footer</nz-footer>\n    </nz-layout>\n\n    <nz-layout>\n      <nz-header>Header</nz-header>\n      <nz-layout>\n        <nz-content>Content</nz-content>\n        <nz-sider>Sider</nz-sider>\n      </nz-layout>\n      <nz-footer>Footer</nz-footer>\n    </nz-layout>\n\n    <nz-layout>\n      <nz-sider>Sider</nz-sider>\n      <nz-layout>\n        <nz-header>Header</nz-header>\n        <nz-content>Content</nz-content>\n        <nz-footer>Footer</nz-footer>\n      </nz-layout>\n    </nz-layout>\n  `,\n  styles  : [ `\n    :host {\n      text-align: center;\n    }\n\n    :host ::ng-deep .ant-layout-header,\n    :host ::ng-deep .ant-layout-footer {\n      background: #7dbcea;\n      color: #fff;\n    }\n\n    :host ::ng-deep .ant-layout-footer {\n      line-height: 1.5;\n    }\n\n    :host ::ng-deep .ant-layout-sider {\n      background: #3ba0e9;\n      color: #fff;\n      line-height: 120px;\n    }\n\n    :host ::ng-deep .ant-layout-content {\n      background: rgba(16, 142, 233, 1);\n      color: #fff;\n      min-height: 120px;\n      line-height: 120px;\n    }\n\n    :host > ::ng-deep .ant-layout {\n      margin-bottom: 48px;\n    }\n\n    :host ::ng-deep .ant-layout:last-child {\n      margin: 0;\n    }\n  ` ]\n})\nexport class NzDemoLayoutBasicComponent {\n}\n"